 *
 * @author
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import { State, Getter, Action, Mutation } from "vuex-class";
import { ElForm } from "element-ui/types/form";

import api from "@/microApps/eventCenter/api";
@Component({
  components: {}
})
export default class AddEventType extends Vue {
  @Prop({ type: String, default: "添加事件定义类型" })
  dialogTitle?: string;

  @Prop({ type: Boolean, default: false })
  dialogVisible!: boolean;

  @Prop({ type: Object, default: () => ({}) })
  data!: any;

  @Watch("dialogVisible")
  handleVisible(newValue: boolean) {
    this.addDialog = newValue;
    if (newValue) {
      this.$nextTick(() => {
        let ref: any = this.$refs.addForm;
        ref.resetFields();
        this.addForm = JSON.parse(JSON.stringify(this.data));
      });
    }
  }

  addDialog: boolean = false;
  loading: boolean = false;
  addForm: any = {
    id: "",
    parentId: "",
    level: 0,
    name: "",
    code: ""
  };
  formRules: any = {
    name: { required: true, trigger: "blur", message: "请输入名称" },
    code: { required: true, trigger: "blur", message: "请输入编码" }
  };

  closeDialog() {
    this.addDialog = false;
    this.$emit("close");
  }

  saveBaseType() {
    (this.$refs.addForm as ElForm).validate(isValidate => {
      if (isValidate) {
        this.loading = true;
        let url = api.addEventType;
        if (this.addForm.id) {
          url = api.updateEventType;
        }
        this.$http
          .post(url, this.addForm)
          .then((res: any) => {
            this.loading = false;
            if (res.data.status) {
              this.$message.success("操作成功");
              this.closeDialog();
              this.$emit("submit");
            } else {
              this.$message.error(res.data.message);
            }
          })
          .catch(() => {
            this.loading = false;
          });
      } else {
        return false;
      }
    });
  }
}
