
































































































/**
 *
 * @author
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import { State, Getter, Action, Mutation } from "vuex-class";
import { ElForm } from "element-ui/types/form";
import { Tag } from "element-ui";
import Utils from "@/utils/utils";

import api from "@/microApps/eventCenter/api";
@Component({
  components: {
    ElTag: Tag
  }
})
export default class AddEventDefinit extends Vue {
  @Prop({ type: String, default: "添加事件定义类型" })
  dialogTitle?: string;

  @Prop({ type: Boolean, default: false })
  dialogVisible!: boolean;

  @Prop({ type: Object, default: () => ({}) })
  data!: any;

  @Prop({ type: Array })
  itemOptions!: Array<any>;

  addDialog: boolean = false;
  loading: boolean = false;

  @Watch("dialogVisible")
  handleVisible(newValue: boolean) {
    this.addDialog = newValue;
    if (newValue) {
      this.$nextTick(() => {
        let ref: any = this.$refs.eventDef;
        ref.resetFields();
        this.eventDef = JSON.parse(JSON.stringify(this.data));
        // 解析设备类型id对应的name
        if (this.eventDef.deviceTypeId) {
          let name: string = Utils.getNameById(this.eventDef.deviceTypeId, this.deviceTypeOptions);
          this.checkedValues = [
            {
              id: this.eventDef.deviceTypeId,
              name: name
            }
          ];
        } else {
          this.checkedValues = [];
        }
      });
    }
  }

  eventProps: any = {
    checkStrictly: true,
    parent: "parentId", // 父级唯一标识
    value: "id", // 唯一标识
    label: "name", // 标签显示
    children: "children" // 子级
  };
  //流程选择
  prcessOptions: Array<any> = [];
  //模板选择
  templateOptions: Array<any> = [];
  //选中的自定义属性
  eventDef: any = {
    id: "",
    eventTypeId: "",
    name: "",
    code: "",
    eventTriggerType: "",
    deviceTypeId: "",
    level: 1,
    // eventTemplateId: "",
    formId: "",
    procDefKey: "",
    handlingFlag: false,
    remark: ""
  };
  eventDefRules: any = {
    eventTypeId: { required: true, message: "请选择事件类别", trigger: "change" },
    name: { required: true, message: "请输入事件名称", trigger: "change" },
    code: { required: true, message: "请输入事件编码", trigger: "change" },
    eventTriggerType: { required: true, message: "请选择事件触发类型", trigger: "change" },
    level: { required: true, message: "请选择事件等级", trigger: "change" },
    // eventTemplateId: { required: true, message: "请选择上报模板", trigger: "change" },
    formId: { required: true, message: "请选择上报表单模板", trigger: "change" },
    procDefKey: { required: true, message: "请选择关联流程", trigger: "change" },
    handlingFlag: { type: "boolean", required: true, message: "请选择是否到受理中心", trigger: "change" }
  };

  formList: any[] = [];

  deviceTypeOptions: Array<any> = [];
  deviceVisible: boolean = false;
  checkedValues: Array<any> = [];

  created() {
    this.getDeviceType();
    this.getAllProcess();
    // this.getAllTemplate();
    this.getAllFormList();
  }

  closeDialog() {
    this.addDialog = false;
    this.$emit("close");
  }

  // 获取设备类型
  getDeviceType() {
    this.$http.get(api.getDeviceType).then((res: any) => {
      if (res.data.status) {
        this.deviceTypeOptions = res.data.data;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }
  //获取相关流程
  getAllProcess() {
    this.$http.get(api.getAllProcessByUserVisible).then((res: any) => {
      if (res.data.status) {
        const result = res.data.data;
        this.prcessOptions = result.records;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }
  //获取相关模板
  getAllTemplate() {
    this.$http
      .post(api.getEventTemplate, {
        size: 9999,
        current: 1
      })
      .then((res: any) => {
        if (res.data.status) {
          const result = res.data.data;
          this.templateOptions = result.records;
        } else {
          this.$message.error(res.data.message);
        }
      });
  }
  // 获取所有表单模板
  async getAllFormList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(this.eventDef.formId ? `${api.getFormListWithDeleted}${this.eventDef.formId}` : api.getFormList, {
      params: {
        size: 999999,
        current: 1
      }
    });
    if (status) {
      this.formList = data.records;
    } else {
      this.$message.error(message);
    }
  }

  // 打开弹窗时初始化选中数据
  initTreeChecked() {
    this.deviceVisible = true;
    this.$nextTick(() => {
      if (this.eventDef.deviceTypeId) {
        (this.$refs["deviceTree"] as any).setCheckedKeys([this.eventDef.deviceTypeId]);
      } else {
        (this.$refs["deviceTree"] as any).setCheckedKeys([]);
      }
    });
  }
  // 点击多选框时调用，只支持单选
  handleNodeChange(node: any, checked: boolean) {
    if (checked) {
      (this.$refs["deviceTree"] as any).setCheckedKeys([node.id]);
    }
  }
  removeItem() {
    this.checkedValues = [];
    this.eventDef.deviceTypeId = "";
  }

  // 点击确定后修改初始化数据
  hasCheckedNodes() {
    this.checkedValues = (this.$refs["deviceTree"] as any).getCheckedNodes();
    if (this.checkedValues.length) {
      this.eventDef.deviceTypeId = this.checkedValues[0].id;
    } else {
      this.eventDef.deviceTypeId = "";
    }
    this.deviceVisible = false;
  }

  //保存事件定义
  saveEventDefinition() {
    (this.$refs.eventDef as ElForm).validate(isValidate => {
      if (isValidate) {
        this.loading = true;
        let url = api.addEventDefinition;
        if (this.eventDef.id) {
          url = api.updateEventDefinition;
        }
        // 页面上的eventTypeId显示是数组类型，传到后端需取最后一个元素的值
        let type: string = typeof this.eventDef.eventTypeId;
        if (type === "object") {
          this.eventDef.eventTypeId = this.eventDef.eventTypeId[this.eventDef.eventTypeId.length - 1];
        }
        this.$http
          .post(url, this.eventDef)
          .then((res: any) => {
            this.loading = false;
            if (res.data.status) {
              this.$message.success("操作成功");
              this.closeDialog();
              this.$emit("submit");
            } else {
              this.$message.error(res.data.message);
            }
          })
          .catch(() => {
            this.loading = false;
          });
      }
    });
  }
}
