 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/eventCenter/api";
import genCascader from "@/utils/genCascader";

import AddEventType from "./components/AddEventType.vue";
import AddEventDefinition from "./components/AddEventDefinit.vue";

@Component({
  components: {
    AddEventType,
    AddEventDefinition
  }
})
export default class EventDefinitionList extends Vue {
  // 类型树信息
  treeMessage: any = {
    filterText: "",
    // tree数据
    data: [],
    // 配置选项
    defaultProps: {
      children: "children",
      label: "name"
    }
  };

  tableList: Array<any> = []; //基础属性表格数据
  searchDTO: any = {
    current: 1,
    size: 10,
    query: {
      eventTypeId: "",
      name: ""
    }
  }; //表格查询参数

  // 添加类型
  dialogTitle = "";
  visibleDialog: boolean = false;
  // 弹窗表单信息
  formData: any = {
    id: "",
    parentId: "",
    level: 0,
    name: "",
    code: ""
  };

  // 自定义属性弹窗相关
  eventVisible: Boolean = false; //弹窗是否显示
  eventTitle: string = ""; //弹窗标题
  eventData: any = {
    id: "",
    eventTypeId: "",
    name: "",
    code: "",
    eventTriggerType: "",
    deviceTypeId: "",
    level: 1,
    eventTemplateId: "",
    procDefKey: "",
    handlingFlag: false,
    remark: ""
  };

  //自定义属性类型
  itemOptions: Array<any> = [];

  // 监听是否tree过滤条件
  @Watch("treeMessage.filterText")
  setFilterText(val: any) {
    (this.$refs.tree as any).filter(val);
  }

  // 过滤tree
  filterNode(value: any, data: any) {
    if (!value) return true;
    return data[this.treeMessage.defaultProps.label].indexOf(value) !== -1;
  }

  // 树节点点击事件
  handleNodeClick(data: any) {
    this.searchDTO.query.eventTypeId = data.id;
    this.getEventDefinitionList();
  }
  // 打开节点弹窗
  openTypeTreeNodeForm(buttonName: any, data: any) {
    switch (buttonName) {
      case "add":
        this.dialogTitle = "添加事件定义类型";
        this.formData = {
          id: "",
          parentId: data.id,
          level: data.level + 1,
          name: "",
          code: ""
        };
        break;
      case "edit":
        this.dialogTitle = "编辑事件定义类型";
        this.formData = Object.assign({}, data);
        break;
      case "addTop":
        this.dialogTitle = "添加事件定义类型";
        this.formData = {
          id: "",
          parentId: "",
          level: 1,
          name: "",
          code: ""
        };
        break;
    }
    this.visibleDialog = true;
  }
  // 根据父组件参数获取类型树数据
  getTypeTreeData() {
    let _this = this;
    this.$http.get(api.getEventTypeTree).then((res: any) => {
      if (res.status) {
        if (res.data.status) {
          _this.treeMessage.data = res.data.data;
          _this.itemOptions = res.data.data;
          // fix级联框问题 去除没有子节点的children
          genCascader(_this.itemOptions);
        } else {
          this.$message.error(res.data.message);
        }
      }
    });
  }
  getEventDefinitionList() {
    this.$http.post(api.getEventDefinition, this.searchDTO).then((res: any) => {
      if (res.data.status) {
        const result = res.data.data;
        this.tableList = result.records;
        this.searchDTO.total = result.total;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }
  search() {
    this.searchDTO.current = 1;
    this.getEventDefinitionList();
  }
  handleSizeChange(val: any) {
    this.searchDTO.size = val;
    this.getEventDefinitionList();
  }
  handleNumChange(val: number) {
    this.searchDTO.current = val;
    this.getEventDefinitionList();
  }

  // 点击删除基础属性类型事件
  deleteBaseType(data: any) {
    this.$confirm("确认删除?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(() => {
      this.$http.get(api.deleteEventType + "/" + data.id).then((res: any) => {
        if (res.data.status) {
          this.$message.success("删除成功");
          // 重新获取列表
          this.getTypeTreeData();
        } else {
          this.$message.error(res.data.message);
        }
      });
    });
  }

  closeDialog() {
    this.visibleDialog = false;
  }
  //保存事件基础属性类型
  saveBaseType() {
    this.getTypeTreeData();
  }

  closeEventDialog() {
    this.eventVisible = false;
  }

  //添加自定义属性
  eventListAdd() {
    this.eventTitle = "添加事件定义";
    this.eventData = {
      id: "",
      eventTypeId: "",
      name: "",
      code: "",
      eventTriggerType: "",
      deviceTypeId: "",
      level: 1,
      eventTemplateId: "",
      procDefKey: "",
      handlingFlag: false,
      remark: ""
    };
    this.eventVisible = true;
  }

  //编辑事件定义
  eventDefinitionEdit(attr: any) {
    this.eventTitle = "编辑事件定义";
    this.eventData = Object.assign({}, attr);
    this.eventVisible = true;
  }

  //保存事件定义
  saveEventDefinition() {
    this.getEventDefinitionList();
  }

  //删除自定义属性
  deleteAttr(attrId: string) {
    this.$confirm("确认删除?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(() => {
      this.$http.get(api.deleteEventDefinition + "/" + attrId).then((res: any) => {
        if (res.data.status) {
          this.$message.success("删除成功");
          this.getEventDefinitionList();
        } else {
          this.$message.error(res.data.message);
        }
      });
    });
  }

  mounted() {
    this.getTypeTreeData();
    this.getEventDefinitionList();
  }
}
